  width: 20%;
  position: fixed;
  background: -webkit-linear-gradient(316deg, rgb(43, 0, 122), rgb(98, 0, 119), rgb(139, 0, 117), rgb(255, 78, 96), rgb(255, 114, 84), rgb(255, 144, 69), rgb(255, 171, 49));
